html, body {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
  background: #000;
  margin: 0;
  padding: 0;
  font-family: Open Sans, Arial, sans-serif;
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

#container {
  width: 100%;
  height: 100%;
}

#main {
  position: absolute;
  inset: 0;
}

canvas {
  cursor: none;
  background-color: #f8f8f8;
  margin: auto;
  padding: 0;
  display: block;
}

canvas.textureSmooth {
  image-rendering: auto;
  image-rendering: optimizequality;
  -ms-interpolation-mode: bicubic;
}

canvas.texturePixelated {
  image-rendering: -webkit-optimize-contrast;
  image-rendering: -o-crisp-edges;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
  -ms-interpolation-mode: nearest-neighbor;
}

.message {
  text-align: center;
  color: #fff;
  vertical-align: bottom;
  z-index: 1;
  background: #6cc27d;
  padding: 1em 2em;
  font-family: monospace;
  font-weight: bold;
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
}

div#menu {
  z-index: 1;
  width: 100%;
  height: 0;
  position: absolute;
}

.menu {
  width: 100%;
  cursor: pointer;
  background: #fff;
  border-bottom: 1px solid #0000001a;
  flex-wrap: wrap;
  transition: all .3s;
  display: flex;
  position: fixed;
}

.playing .menu {
  opacity: 0;
}

.playing .menu:hover {
  opacity: .9;
}

.menu ul {
  visibility: hidden;
  opacity: 0;
  z-index: 1;
  background-color: #f5f5f5;
  transition: all .3s;
  position: absolute;
  top: 100%;
  left: 0;
  box-shadow: 0 5px 10px #0000001a;
}

li.hide {
  display: none !important;
}

.menu ul ul {
  top: 0;
  left: 100%;
}

#menu_top li {
  padding: .7em 1em;
}

.menu ul li {
  padding: .3em 1em !important;
}

.menu li {
  white-space: nowrap;
  cursor: pointer;
  position: relative;
}

.menu li:hover {
  background: #0000001a;
}

.menu li:hover > ul {
  visibility: visible;
  opacity: 1;
}

.touch-controls {
  padding: 1em;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.touch-controls button {
  -webkit-appearance: none;
  opacity: .7;
  width: 3em;
  height: 3em;
  text-align: center;
  cursor: pointer;
  background: #fff;
  border: 0;
  outline: 0;
  margin: .5em;
  line-height: 3em;
  transition: all .1s;
  display: inline-block;
}

.touch-controls button:active {
  transform: scale(.95);
  box-shadow: inset 0 0 10px #0006;
}

.touch-dpad, .touch-buttons {
  text-align: center;
  flex-grow: 1;
}

.touch-buttons {
  align-self: flex-end;
}

.touch-dpad--up {
  width: 100%;
  flex-grow: 1;
}

@media screen and (min-aspect-ratio: 1) {
  .touch-controls {
    display: none;
  }

  #main {
    flex-grow: 1;
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

